import React, { useState } from "react";
import Portal from "./Portal";
import styled from "styled-components";
import * as S from "./style";
import { ReactComponent as Close } from "../../../svg/close2.svg";
import { ReactComponent as Check } from "../../../svg/check.svg";
import RedAlert from "./RedAlert";
import { reviewReportApi } from "../../../util/review";

export default function ReportReason({ setModal, id }) {
  const closeModal = () => {
    setModal(false);
  };
  const [selected, setSelected] = useState({});
  const [report, setReport] = useState(false);

  const list = [
    "   ",
    ",    ",
    "   ",
    "  ",
    " ,    ( )",
    "  ",
    "",
  ];

  const onSelect = (item, i) => {
    setSelected({ content: item, id: i + 1 });
  };

  const onReport = () => {
    reviewReportApi(id, selected.id)
      .then((res) => {
        window.alert("");
        setModal(false);
      })
      .catch((err) => {
        window.alert("  ");
        window.location.reload();
      });
  };

  return (
    <>
      <Portal>
        <S.ModalBox>
          <S.ModalHeader>
            <p> </p>
            <Close onClick={closeModal} />
          </S.ModalHeader>
          <S.ModalContent>
            <TextBox>
              <p>  .</p>
              <p>       .</p>
            </TextBox>

            {list.map((item, i) => (
              <RItem
                key={i}
                onClick={() => onSelect(item, i)}
                isSelected={selected.content === item}
              >
                <p>{item}</p>
                <Check />
              </RItem>
            ))}
          </S.ModalContent>
          <ModalFooter>
            <S.Btn color={"#2563eb"} onClick={() => setReport(true)}>
              
            </S.Btn>
          </ModalFooter>
        </S.ModalBox>
      </Portal>
      {report && (
        <RedAlert
          text={" "}
          text1={""}
          text2={" "}
          text3={"?"}
          setAlert={setReport}
          func={onReport}
          forFunc={null}
        />
      )}
    </>
  );
}

const ModalFooter = styled.div`
  display: flex;
  width: 100%;
  box-sizing: border-box;
  justify-content: flex-end;
  align-items: baseline;
  & > p {
    color: #acacac;
    font-size: 16px;
  }
`;

const TextBox = styled.div`
  & > p:first-child {
    font-size: 16px;
    color: #f6f6f6;
    font-weight: 700;
    line-height: 28px;
  }
  & > p:nth-child(2) {
    font-size: 14px;
    color: #8b8b8b;
  }
  margin-bottom: 24px;
`;

const RItem = styled.div`
  display: flex;
  justify-content: space-between;
  height: 56px;
  padding: 0 24px;
  cursor: pointer;
  align-items: center;
  & > p {
    color: ${(props) => (props.isSelected ? "#FC6406" : "#E3E3E3")};
  }

  & > svg {
    path {
      fill: ${(props) => (props.isSelected ? "#FC6406" : "#E3E3E3")};
    }
  }
`;
